export const ClipBoard_Text = [
  '테스트 링크가 복사되었습니다.',
  '결과 링크가 복사되었습니다.'
]

let $start = null;
let $check = null;
let $loading = null;
let $result = null;
let $alert_copy = null;

export const getSelector = ()=>{
  $start = document.querySelector("#Start");
  $check = document.querySelector("#Check");
  $loading = document.querySelector("#Loading");
  $result = document.querySelector("#Result");
  $alert_copy = document.querySelector(".alert_copy");
}

let alert_copy_Timeout = null;
export const copyMessage = () => {
  clearTimeout(alert_copy_Timeout);
  $alert_copy.style.transition= '0';
  console.log($alert_copy.style.transition);
  $alert_copy.style.opacity = 0;
  $alert_copy.style.transform = 'scale(0)';

  $alert_copy.style.transition= '0.2s';
  console.log($alert_copy.style.transition);
  $alert_copy.style.opacity = 1;
  $alert_copy.style.transform = 'scale(1)';

  alert_copy_Timeout = setTimeout(() => {
  $alert_copy.style.opacity = 0;
    $alert_copy.style.transform = 'scale(0.5)';
  }, 3000)
}

let timeout = null;

export default createStore({
  state: {
    page_Type: Page_Type.Start,
    question: [
      ["카카오톡 이모티콘을 구매할 수 있다.", 2, 1],
      ["인스타그램에서 공구를 해본 적 있다.", 2, 1],
      ["정보를 얻기 위해 네이버 밴드를 이용한 적 있다.", 5, 1],
      ["SNS에 게시물을 올려본 적 있다.", 5, 1],
      ["궁금한 점이 있으면 포털을 이용한다.", 2, 1],
      ["홈페이지와 어플의 다른 점을 1개 이상 알고 있다.", 10, 1],
      ["스스로 원하는 앱을 찾아 설치할 수 있다.", 5, 1],
      ["큐알코드를 이용해 웹사이트에 접속할 수 있다.", 5, 1],
      ["스마트폰으로 사진을 촬영하고, 편집해본 적 있다.", 10, 1],
      ["스마트폰으로 영상 편집을 해본 적 있다.", 10, 1],
      ["새로운 이메일 계정을 만들 수 있다.", 5, 1],
      ["크롬이 무엇인지 알고 있다.", 5, 1],
      ["컴퓨터만 구매하면 인터넷이 자동으로 연결된다.", 2, 0],
      ["와이파이를 잡아 무료로 인터넷을 사용해본 적 있다.", 2, 1],
      ["인터넷에서 직접 로그인을 하여 물건을 구매해본 적 있다.", 2, 1],
      ["물건을 판매하기 위해 인터넷에 상점을 만들어본 적 있다. ", 5, 1],
      ["카카오톡 이모티콘은 카카오 회사만 직접 만들 수 있다. ", 5, 0],
      ["웹사이트 링크를 주변 사람에게 공유할 수 있다.", 2, 1],
      ["스마트폰으로 화면을 캡쳐할 수 있다.", 2, 1],
      ["키오스크 이용은 어렵다.", 10, 0],
      ["컴퓨터로 게임을 직접 다운 받을 줄 안다.", 2, 1],
      ["스마트폰으로 쿠폰을 적립해 할인 받은 적 있다.", 2, 1],
    ],
    cur_question_index: 0,
    score: 0,
    cur_Page: null,
    result_Score_Type: null,
    clipboard_Text: null,
  },
  mutations: {
    [Go_Start](state) {
      clearTimeout(timeout);
      state.page_Type = Page_Type.Start;
      const $cur_Page = state.cur_Page;
      if($cur_Page) {
        this.dispatch(Page_Change, { $cur_Page, $start });
      }
    },
    [Start_Question](state) {
      state.score = 0;
      state.cur_question_index = 0;
      clearTimeout(timeout);
      state.page_Type = Page_Type.Check;
      this.dispatch(Page_Change, { $start, $check });
    },
    [Next_Question](state, answer) {
      if(state.question[state.cur_question_index][2] == answer) {
        state.score += state.question[state.cur_question_index][1];
      }
      if(state.cur_question_index >= state.question.length - 1){        
        state.page_Type = Page_Type.Loading;
        this.dispatch(Page_Change, { $check, $loading });
        timeout = setTimeout(()=> {
          let type = null;
          if(state.score > 80){
            type = Result_Type.디지털원주민;
          } else if(state.score > 60) {
            type = Result_Type.디지털학도;
          } else {
            type = Result_Type.디지털이방인;
          }
          this.commit(Go_Result, type)
        }, 3000);
        return;
      }
      state.cur_question_index++;
    },
    [Go_Result](state, result) {
      if([Result_Type.디지털원주민, Result_Type.디지털학도, Result_Type.디지털이방인, "디지털원주민", "디지털학도", "디지털이방인"].includes(result)){
        if(!result.includes(' ')) {
          result = `${result.substring(0, 3)} ${result.substring(3)}`
        }
        state.page_Type = Page_Type.Result;
        state.result_Score_Type = result;
        let $cur_page = this.state.cur_Page;
        if(!$cur_page) {
          $cur_page = $start;
        }
        this.dispatch(Page_Change, { $cur_page, $result });
      }else {
        state.page_Type = Page_Type.Start;
        const $Null = null;
        this.dispatch(Page_Change, { $Null, $start });
      }
    },
    [Params_Result](state, result) {
      if([Result_Type.디지털원주민, Result_Type.디지털학도, Result_Type.디지털이방인, "디지털원주민", "디지털학도", "디지털이방인"].includes(result)){
        if(!result.includes(' ')) {
          result = `${result.substring(0, 3)} ${result.substring(3)}`
        }
        state.page_Type = Page_Type.Result;
        state.result_Score_Type = result;
        let $cur_page = this.state.cur_Page;
        if(!$cur_page) {
          $cur_page = $start;
        }
        this.dispatch(Page_Change, { $cur_page, $result });
      }else {
        state.page_Type = Page_Type.Start;
        const $Null = null;
        this.dispatch(Page_Change, { $Null, $start });
      }
      router.push({
        path: '/'
      });
    },
    [ClipBoard_Change](state, text) {
      console.log()
      state.clipboard_Text = text;
    }
  },
  actions: {
    [Page_Change] : function(context, $page){
      $page = Object.entries($page);
      if($page[0] && $page[0][1]) {
        const $old = $page[0][1];
        $old.style.transform = "scale(0)";
        $old.style.opacity = 0;
        if($old === $result) {
          $old.style.display = "none";
        }
      }
      const $new = $page[1][1];
      if($new === $result){
        $new.style.display = "block";
      }
      $new.style.transform = "scale(1)";
      $new.style.opacity = 1;
      this.state.cur_Page = $new;
    }
  },
  modules: {
  }
})
